.gbpe-cta {
  margin-top: var(--gbpe-cta-space-top, 0);
  margin-top: var(--gbpe-cta-space-bottom, 0);
}
.gbpe-cta__inner {
  padding: var(--gbpe-cta-ve-space, 65px) var(--gbpe-cta-ho-space, 50px);
  border-radius: var(--gbpe-cta-border-radius, 5px);
  border: 1px solid var(--gbpe-cta-border-color, #f4f7fe);
  display: flex;
  gap: 20px;
  align-items: center;
}
.gbpe-cta__desc {
  width: 75%;
}
.gbpe-cta__link {
  flex: 0 0 auto;
  text-align: right;
  width: 25%;
}
.gbpe-cta__link a {
  background: var(--gbpe-cta-primary-color, #1b44bf);
  border: 1px solid var(--gbpe-cta-primary-color, #1b44bf);
  border-radius: var(--gbpe-cta-border-radius, 5px);
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-size: 15px;
  line-height: 1.5;
  padding: 12px 35px;
  transition: background 0.3s ease, color 0.3s ease;
  vertical-align: middle;
  text-decoration: none;
  text-align: center;
  color: #fff;
}
.gbpe-cta__link a:hover {
  text-decoration: none;
}
.gbpe-cta__title {
  line-height: 1.2;
  margin-bottom: 0.5rem;
}
.gbpe-cta__text {
  margin-bottom: 0;
}

.gbpe-headline {
  padding-top: var(--gbpe-headline-padding-top, 80px);
  padding-bottom: var(--gbpe-headline-padding-bottom, 80px);
}
.gbpe-headline__inner {
  text-align: var(--gbpe-headline-text-align, left);
}
.gbpe-headline__before {
  border: 1px solid #dee2e6;
  display: inline-block;
  font-weight: 500;
  color: var(--dt-primary-color);
  padding: 8px 20px 8px 20px;
  border-radius: 10px;
  margin-bottom: 10px;
}
.gbpe-headline__title {
  margin-top: 0;
  line-height: 1.2;
  margin-bottom: 0.5rem;
}
.gbpe-headline__desc {
  font-size: 16px;
  margin-bottom: 25px;
  margin-top: 0;
  margin-bottom: 0;
}